// @/view/idle/idle-index.vue    //闲置首页
<template>
    <div style="width: 100%;">
        <div class="container">
            <ul class="idleList">
                <li v-for="(item,index) in idleList" :key="index" @click="handleEnter(item.idleId)">
                    <img :src="item.idleImg">
                    <div class="info">
                        <p class="idleName">
                            {{item.idleName}}
                        </p>
                        <p class="price">
                            <span>¥</span> {{item.idlePrice}}
                        </p>
                        <p class="address">
                            <span>{{item.idleCity}}</span>
                            <span>{{item.idleDistrict}}</span>
                        </p>
                        <p class="user">
                            <span>{{item.time}} · {{item.userName}}</span>
                        </p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import appMixins from "@/assets/js/appMixins.js";
export default {
  name: "idle-index",
  mixins: [appMixins],
  components: {},
  data() {
    return {
      idleList: []
    };
  },
  methods: {
    // 接收data
    getData(data) {
      this.idleList = data;
    },
    handleEnter(id) {
      console.log(id);
    }
  },
  mounted() {
    const list = [
      {
        idleId: 110,
        idleImg:
          "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
        idleName: "未拆封全新红米note 4x",
        idlePrice: "2910",
        idleCity: "天津",
        idleDistrict: "河西区",
        time: "1小时前",
        userName: "青春年少时"
      },
      {
        idleId: 120,
        idleImg:
          "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
        idleName: "未拆封全新红米note 4x",
        idlePrice: "2910",
        idleCity: "天津",
        idleDistrict: "河西区",
        time: "1小时前",
        userName: "青春年少时"
      },
      {
        idleId: 130,
        idleImg:
          "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
        idleName: "未拆封全新红米note 4x",
        idlePrice: "2910",
        idleCity: "天津",
        idleDistrict: "河西区",
        time: "1小时前",
        userName: "青春年少时"
      },
      {
        idleId: 140,
        idleImg:
          "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
        idleName: "未拆封全新红米note 4x",
        idlePrice: "2910",
        idleCity: "天津",
        idleDistrict: "河西区",
        time: "1小时前",
        userName: "青春年少时"
      }
    ];
    this.getData(list);
  }
};
</script>

<style lang="less" scoped>
@m: 750/375;
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px solid transparent;
  font-size: 14px * @m;
  margin-top: 30px * @m;
  background: #ffffff;
  height: auto;
}

.idleList {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  box-sizing: border-box;
}

.idleList li {
  width: 162px * @m;
  height: 250px * @m;
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.5);
  border-radius: 6px * @m;
  box-sizing: border-box;
  margin-bottom: 20px * @m;
}

.idleList li img {
  display: inline-block;
  width: 162px * @m;
  height: 108px * @m;
  border-top-left-radius: 6px * @m;
  border-top-right-radius: 6px * @m;
}

.idleList li .info {
  width: 100%;
  /* background-color: red; */
  padding: 10px * @m 10px * @m 0 10px * @m;
  box-sizing: border-box;
}

.idleList li .info p {
  margin-bottom: 5px * @m;
}

.idleList li .info span {
  color: #c6c6c6;
  font-size: 13px * @m;
}

.idleList li .info .idleName {
  font-size: 16px * @m;
  height: 40px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: off */
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.idleList li .info .price {
  font-size: 18px * @m;
  color: #ff7474;
}

.idleList li .info .price span {
  font-size: 13px * @m;
  color: #ff7474;
}

.idleList li .info .user {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



